<template>
  <v-app>
    <v-content>
      <v-btn v-if="mobile" @click.stop="drawer = !drawer" class="cart-btn" icon>
        <v-badge
          :value="cart.length"
          color="#f64747"
          :content="cart.length"
          left overlap
          transition="slide-x-transition">
          <v-icon>mdi-basket</v-icon>
        </v-badge>
      </v-btn>

      <v-text-field
        class="search my-6"
        single-line
        solo
        v-model="search"
        placeholder="...پسته، بادام ،فندق و"
        append-icon="mdi-magnify"
        @change="getProducts"
        @input="getProducts"
      ></v-text-field>

      <Filters @updated="updateFilters"/>

      <v-row no-gutters class="">
        <v-col :lg="cart.length? 9 : 12" :md="cart.length? 8 : 12" :sm="cart.length? 8 : 12" class="right-col">
          <div class="title my-6 block-title">
            <span>همه</span>
          </div>

          <Products :products="products" @cart="updateCart"/>
        </v-col>

        <v-col lg="3" md="4" sm="4" class="pa-4 left-col mt-10" v-if="cart.length && !mobile">
          <Cart :cart="cart"
                @remove="v => cart.splice(cart.indexOf(v), 1)"/>
        </v-col>
      </v-row>

      <v-navigation-drawer
        v-model="drawer"
        fixed
        temporary
        v-if="cart.length && mobile"
      >
        <Cart :cart="cart"
              class="navbarr"
              @remove="v => cart.splice(cart.indexOf(v), 1)"/>
      </v-navigation-drawer>
    </v-content>
  </v-app>
</template>

<script>
import Products from './components/Products';
import Filters from './components/Filters';
import Cart from './components/Cart';
import api from './api'

export default {
  name: 'App',
  components: {
    Products,
    Filters,
    Cart,
  },

  data: () => ({
    selectedFilters: [],
    products: [],
    cart: [],
    search: '',
    drawer: false,
    mobile: false
  }),
  created() {
    window.addEventListener('resize', this.onResize)
  },

  beforeDestroy() {
    window.removeEventListener('resize', this.onResize)
  },
  mounted(){
    this.getProducts()
    this.onResize()
  },

  methods:{
    updateFilters(vars){
      this.selectedFilters = vars
      this.getProducts()
    },

    async getProducts(){
      let params = {}
      if(this.selectedFilters.length){
        let tags = this.selectedFilters.join(',')
        params['tags'] = tags
      }
      if(this.search){
        params['search'] = this.search
      }
      this.products = await api.getProducts(params).then(res => res.products)
    },

    updateCart(products){
      this.cart = products
    },

    onResize() {
      if (window.innerWidth < 768) {
        this.mobile = true
      } else {
        this.mobile = false
      }
    }
  },
};
</script>
