<template>
  <v-container>
    <v-layout align-center justify-space-around class="filters">
      <v-checkbox value="0" @click="selected = []; clicked()" class="all">
        <template v-slot:label>
          <v-card ripple shaped
                  width="130px"
                  class="pa-2 tags"
                  :class="{'selected': selected.length == 0}">
            <v-img src="@/assets/nuts.png"
                   aspect-ratio="1"
                   contain
                   max-width="70"
                   class="mx-auto">
            </v-img>
            <v-card-title class="justify-center py-1">همه</v-card-title>
          </v-card>
        </template>
      </v-checkbox>

      <v-checkbox v-for="item in filters.slice(0, 8)"
                  :key="item.tid"
                  v-model="selected"
                  :value="item.tid"
                  @click="clicked"
      >
        <template v-slot:label>
          <v-card ripple shaped
                  width="130px"
                  class="pa-2 tags"
                  :class="{'selected': selected.indexOf(item.tid) > -1}">
            <v-img :src="item.uri"
                   aspect-ratio="1"
                   contain
                   max-width="70"
                   class="mx-auto">
            </v-img>
            <v-card-title class="justify-center py-1">{{item.name}}</v-card-title>
          </v-card>
        </template>
      </v-checkbox>
    </v-layout>
  </v-container>
</template>

<script>
import api from '../api'

export default {
  name: 'filters',
  data () {
    return {
      filters:[],
      selected:[],
    }
  },
  mounted() {
    this.getFilters()
  },
  methods:{
    async getFilters(){
      this.filters = await api.getFilters()
    },
    clicked(){
      this.$emit('updated', this.selected)
    },
  },
}
</script>
<style lang="scss">
.filters{
  .v-messages,
  .v-input--selection-controls__input {
    display: none;
  }
  .tags{
    border-radius: 12px;
    box-shadow: 13.5px 17.3px 31px 0 rgba(0, 0, 0, 0.08);
    background-color: #fbfbfb;
    border: solid 3px #fbfbfb;
    &.selected{
      border-color: #d1b550;
      background-image: linear-gradient(55deg, #e8d183 44%, #bcaa6a 113%);
      color: #fff;
      .v-image__image {
        filter: brightness(5);
      }
    }
  }
  .all .v-image__image {
    filter: brightness(0.5);
  }
  .v-input .v-label{
    height: auto;
  }
}
</style>


