<template>
  <div class="products">
    <v-card shaped
            v-for="item in products" :key="item.nid"
            class="d-inline-flex product justify-space-between my-4">

      <v-img src="@/assets/product-bg.png" class="before"></v-img>

      <v-btn class="add" icon
            @click="addToCart(item)"
            :class="item.status == 'available'? '': 'lock'">
        <v-icon color="#fff" size="20px">{{item.status == 'available'? 'mdi-plus': 'mdi-lock-outline'}}</v-icon>
      </v-btn>

      <v-btn class="remove" icon
             v-if="selectedProducts.filter(p => p.nid == item.nid).length"
             @click="removeFromCart(item)">
        <v-icon color="#fff" size="18px">mdi-minus</v-icon>
      </v-btn>

      <v-flex class="d-flex flex-column justify-space-between py-2" style="z-index: 1;">
        <v-card-title class="py-0" color="#6f6b67" style="word-break: keep-all;">{{item.title}}</v-card-title>

        <v-flex class="d-flex justify-space-between pl-3 align-center">
          <span>{{item.qty}}</span>
          <span v-if="item.status == 'available' && item.list_price > item.price"
                class="list-price"
          >{{item.list_price | currency}}</span>
        </v-flex>

        <div class="text-center price" v-if="item.status == 'available'">{{item.price | currency}} تومان</div>
        <div class="text-center unavailable" v-else>اتمام موجودی</div>

      </v-flex>

      <div class="img-part">
        <v-img src="@/assets/product-img-bg.png" class="img-bg"></v-img>
        <v-img :src="item.picture"
               aspect-ratio="1"
               class="product-image">
        </v-img>
        <span class="overlay" v-if="selectedProducts.filter(p => p.nid == item.nid).length">
          {{selectedProducts.filter(p => p.nid == item.nid)[0].qty * 0.25}}
          <span style="font-size: 13px; margin-bottom: -10px;">کیلوگرم</span>
        </span>

        <span class="sale"
              v-if="item.list_price > item.price">
        <v-icon color="#fdb62f" size="25px">mdi-sale</v-icon>
      </span>
      </div>
    </v-card>
    <div class="d-inline-flex justify-space-between my-4"></div>
    <div class="d-inline-flex justify-space-between my-4"></div>
  </div>
</template>

<script>
// import api from '../api'

export default {
  name: 'Products',
  props:['products'],
  data: () => ({
    selectedProducts: []
  }),
  mounted() {
    // this.getProducts()
  },
  methods:{
    addToCart(product){
      let a = this.selectedProducts.filter(item => item.nid == product.nid)
      if(a.length)
        a[0].qty += 1
      else
        this.selectedProducts.push({
          nid: product.nid,
          qty: 1,
          title: product.title,
          picture: product.picture,
          price: product.price,
          list_price: product.list_price,
        })
      this.$emit('cart', this.selectedProducts)
    },
    removeFromCart(product){
      let a = this.selectedProducts.filter(item => item.nid == product.nid)[0]
      if(a.qty == 1)
        this.selectedProducts.splice(this.selectedProducts.indexOf(a), 1)
      else
        a.qty -= 1
      this.$emit('cart', this.selectedProducts)
    },
  },
};
</script>
