<template>
  <v-card
    class="mx-auto cart"
    width="300"
    tile shaped
  >
    <div class="bg"></div>
    <v-list class="pt-0" flat>

      <v-subheader class="justify-center">
        <span class="ml-8" style="font-size: 20px;">سبد خرید</span>
        <v-badge
          :value="cart.length"
          color="#f64747"
          :content="cart.length"
          left overlap
          transition="slide-x-transition"
        >
          <v-icon>mdi-basket</v-icon>
        </v-badge>
      </v-subheader>

      <v-list-item-group v-for="(item, i) in cart" :key="i">
        <v-list-item class="py-2" :ripple="false">
          <v-list-item-content>
            <v-row no-gutters class="align-center">
              <v-btn icon @click="removeFromCart(item)" class="ml-4">
                <v-icon color="#f64747">mdi-close</v-icon>
              </v-btn>

              <v-col style="width: calc(66.66% - 40px)">
                <v-list-item-title  class="pb-4">{{item.title}}</v-list-item-title>
                <span>{{item.price | currency}}  تومان</span>
              </v-col>

              <div class="align-center d-flex flex-column" style="width: 70px">
                <v-row class="pb-4" no-gutters>
                  <v-btn icon x-small @click="addProduct(item)">
                    <v-icon>mdi-plus</v-icon>
                  </v-btn>

                  <span style="width: 30px;text-align: center; white-space: nowrap;">{{item.qty * 0.25}}</span>

                  <v-btn icon x-small @click="removeProduct(item)">
                    <v-icon>mdi-minus</v-icon>
                  </v-btn>
                </v-row>
                <span>کیلوگرم</span>
              </div>
            </v-row>

          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
      </v-list-item-group>

      <v-list-item-group class="bottom-section">
        <div class="d-flex justify-space-between px-5 py-6">
          <span style="font-weight: bold;" class="title">قابل پرداخت</span>
          <span class="price">{{calculateCart | currency}} تومان</span>
        </div>
        <v-btn rounded class="mb-3 d-block mx-auto"
               :href="cartLink"
               elevation="0">ثبت و تکمیل سفارش</v-btn>
      </v-list-item-group>

    </v-list>
  </v-card>
</template>

<script>
// import api from '../api'

export default {
  name: 'Cart',
  props:['cart'],
  data: () => ({
  }),
  mounted() {
    // this.getProducts()
  },
  computed:{
    calculateCart: function() {
      if(this.cart.length)
        return this.cart.map(item => item.qty * item.price).reduce((a,b) => a + b, 0)
      else
        return 0
    },
    cartLink: function(){
      if(this.cart.length)
        return 'http://nutland-co.com/cart/add/e-' +
          this.cart.map(item => 'p' + item.nid + '_q' + item.qty).reduce((a,b) => a + b + '-', '').slice(0, -1) +
          '?destination=cart'
      else
        return ''
    },
  },
  methods:{
    removeFromCart(item){
      console.log(item)
      this.$emit('remove', item)
    },
    addProduct(product){
      this.cart.filter(item => item.nid == product.nid)[0].qty +=1
    },
    removeProduct(product){
      let a = this.cart.filter(item => item.nid == product.nid)[0]
      if(a.qty == 1)
        this.cart.splice(this.cart.indexOf(a), 1)
      else
        a.qty -= 1
    },
  },
};
</script>
